import React, {useContext} from 'react'
import { AtmObject } from '../App'
import Card from './card'

function Deposit() {
  const atmObject = useContext(AtmObject)
  const { deposit, setDeposit, accounts, setAccounts, user} = atmObject;
  const handleDeposit = ()=>{
      let newDeposit = document.getElementById('deposit').value;
      setDeposit(newDeposit);
  }
  const depositMoney = ()=>{
    if (isNaN(deposit)) {
			setDeposit("");
			alert("Error, the amount must be a numeric value");
			return;
		}
    if (deposit<0) {
			setDeposit("");
			alert("Error, the amount cannot be a negative value");
			return;
		}
    let newAmount = accounts[user].accountBalance + Number(deposit);
    alert(`Success! $${deposit} were depositted into your account. Your new total is: ${newAmount}`)
    setAccounts(accounts, accounts[user].accountBalance = newAmount);
    setDeposit(0);
    document.getElementById('deposit').value = 0;
  }
  return (
    <Card
    bgcolor="success"
    header="Deposit"
    text = "Add funds to your account"
    body={
      <> 
        <h2 className = "account-balance">Account Balance:<br/><span className='balance-amount'>${(accounts[user].accountBalance)}</span></h2>
        <div id="deposit-amount-div">
            <h3>Deposit Amount: <span className='balance-amount'>${deposit}</span></h3>
            <input id = 'deposit' onChange = {handleDeposit}></input>
            {/* If we are not satisfied with alerts, we can also limit input by: type = 'number' */}
            <button type = 'button' className="btn btn-light" id = 'deposit-button' disabled={deposit<=0} onClick = {depositMoney}>Deposit</button>
            {/* If we are not satisfied with alerts, we can also limit input by: disabled={deposit<=0} */}
        </div>
      </>
    } 
  />
  )
};
export default Deposit