import React from 'react'
import Card from './card'
import { AtmObject } from '../App'

function CreateAccount(){
  const atmObject = React.useContext(AtmObject);
  const { accounts, setAccounts} = atmObject
  const [show, setShow]         = React.useState(true);
  const [status, setStatus]     = React.useState('');
  const [name, setName]         = React.useState('');
  const [email, setEmail]       = React.useState('');
  const [password, setPassword] = React.useState('');

  function validate(field, label){
      if (!field) {
        setStatus('Error: ' + label);
        setTimeout(() => setStatus(''),3000);
        return false;
      }
      if (label==='password'&& field.length<8){
        setStatus('Error: password is too short, at least 8 characters are required!');
        setTimeout(() => setStatus(''),3000);
        return false;
      }
      if (label==='email'&& !/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]{2,4}$/.test(field)){
        setStatus('Error: Email is invalid!');
        setTimeout(() => setStatus(''),3000);
        return false;
      }
      if (label==='email'&& accounts[email]) {
        setStatus('Error: Email already exists!');
        setTimeout(() => setStatus(''),3000);
        return false;
      }
      return true;
  }
  
  function handleCreate(){
    if (!validate(name, 'name')) return;
    if (!validate(email, 'email')) return;
    if (!validate(password, 'password')) return;
    setAccounts(accounts,accounts[email] = {email: email, name: name, password:password, accountBalance: 100});
    setShow(false);
  }    

  function clearForm(){
    setName('');
    setEmail('');
    setPassword('');
    setShow(true);
  }

  return (
    <Card
      bgcolor="primary"
      header="Create Account"
      status={status}
      body={show ? (  
              <>
              Name<br/>
              <input type="input" className="form-control" id="name" placeholder="Enter name" value={name} onChange={e => setName(e.currentTarget.value)} /><br/>
              Email address<br/>
              <input type="input" className="form-control" id="email" placeholder="Enter email" value={email} onChange={e => setEmail(e.currentTarget.value)}/><br/>
              Password<br/>
              <input type="password" className="form-control" id="password" placeholder="Enter password" value={password} onChange={e => setPassword(e.currentTarget.value)}/><br/>
              <button type="submit" className="btn btn-light"  disabled={!name || !email || !password} onClick={handleCreate}>Create Account</button>
              </>
            ):(
              <>
              <h5>Success</h5>
              <button type="submit" className="btn btn-light" onClick={clearForm}>Add another account</button>
              </>
            )}
    />
  )
}
export default CreateAccount;