import React from 'react';
import logo from './logo.png';
import reactlogo from './react.png';
import alldataimg from './alldata.png'; 
import mobileapp from './mobileapp.png'
import withdrawal from './withdrawal.png'; 
import depositing from './depositing.png'; 

function Logo() {
    return <img src={logo} className="Img-carousel" alt="Logo" />;
}
function ReactLogo() {
    return <img src={reactlogo} className="App-logo" alt="Reactlogo" />;
}
function MobileApp() {
    return <img src={mobileapp} className="Img-carousel" alt="MobileApp" />;
}
function DepositImg() {
    return <img src={depositing} className="Img-carousel" alt="Logo" />;
}
function WithdrawImg() {
    return <img src={withdrawal} className="Img-carousel" alt="withdrawlogo" />;
}
function AllDataImg() {
    return <img src={alldataimg} className="Data-img " alt="alldata" />;
}

export {Logo,ReactLogo,MobileApp,WithdrawImg,DepositImg,AllDataImg};