import React, {useContext} from 'react'
import { AtmObject } from '../App'

function Logout() {
    const atmObject = useContext(AtmObject)
    const { isLoggedIn, setIsLoggedIn } = atmObject
    function logout (){
        setIsLoggedIn(false);
    }
    return (
        !isLoggedIn ? (  
            <>
            Succesfully Logged-Out
            </>
        ):(
            <>
            <br/>
            <h5>Are you sure you want to log-out?</h5>
            <br/>
            <button type = 'button' className="btn btn-light" id = 'logging-out' onClick = {logout}>Logout</button>
            <br/>
            </>
        )
    )
}

export default Logout
