import React from 'react'
import Card from './card'
import { AtmObject } from '../App'

function Login(){
  const atmObject = React.useContext(AtmObject);
  const {isLoggedIn, setIsLoggedIn, accounts, user, setUser} = atmObject
  const [status, setStatus]     = React.useState('');
  const [password, setPassword] = React.useState('');
  const [email, setEmail]       = React.useState('');

  function validate(field, label){
    if (!field) {
      setStatus('Error: ' + label);
      setTimeout(() => setStatus(''),3000);
      return false;
    }
    return true;
}
  function handleLogin(){
    console.log(email);
    if (!validate(email, 'email')) return;
    if (!validate(password, 'password')) return;  

    if (!accounts[email]) {
      alert(`${email} does not exist, please create an account`);
      return 
    } 
    if (accounts[email].password===password) {
      console.log(`logged in user: ${email}`)
      setIsLoggedIn(true)
      setUser(email)
    };
  } 

  return (
    <Card
      bgcolor="primary"
      header="Login"
      status={status}
      body={!isLoggedIn ? (  
              <>
              Email address<br/>
              <input type="input" className="form-control" id="email" placeholder="Enter email" value={email} onChange={e => setEmail(e.currentTarget.value)}/><br/>
              Password<br/>
              <input type="password" className="form-control" id="password" placeholder="Enter password" value={password} onChange={e => setPassword(e.currentTarget.value)}/><br/>
              <button type="submit" className="btn btn-light" onClick={handleLogin}>Login</button>
              </>
            ):(
              <>
              <h5>Welcome {accounts[user].name}!</h5>
              <h6>You are now Logged-In!</h6>
              </>
            )}
    />
  )
}
export default Login