import React, { useContext } from 'react'
import { AtmObject } from '../App'
import Card from './card'

function Withdraw() {
  const atmObject = useContext(AtmObject)
  const { withdraw, setWithdraw, accounts, setAccounts, user} = atmObject;
  const handleWithdraw = ()=>{
    let newWithdraw = document.getElementById('withdraw').value;
    setWithdraw(newWithdraw);
  }
  const withdrawMoney = ()=>{
    if (isNaN(withdraw)) {
			setWithdraw("");
			alert("Error, the amount must be a numeric value");
			return;
		}
    if (Number(withdraw)>accounts[user].accountBalance) {
      alert(`Transaction failed due to insufficient funds. Please enter a number lower than ${accounts[user].accountBalance}`);
      return
    }
    let newAmount = accounts[user].accountBalance - Number(withdraw);
    alert(`Success! $${withdraw} were withdrawn from your account. Your new total is: ${newAmount}`);
    setAccounts(accounts, accounts[user].accountBalance = newAmount);
    setWithdraw(0);
    document.getElementById('withdraw').value = 0;
    
  }
  return (
    <Card
      bgcolor="primary"
      header="Withdraw"
      text = "Take funds out of your account"
      body={
        <> 
            <h2 className = "account-balance">Account Balance:<br/><span className='balance-amount'>${accounts[user].accountBalance.toLocaleString('en-us')}</span></h2>
            <h3>Withdraw Amount: <span className='balance-amount'>${withdraw}</span></h3>
            <input type = 'number' id = 'withdraw' onChange = {handleWithdraw}></input> 
            {/* If we are not satisfied with alerts, we can also limit the input by: type = 'number' disabled={withdraw<=0||accounts[user].accountBalance-Number(withdraw)<0} */}
            <button type = 'button' className="btn btn-light" id = 'withdraw-button'  onClick = {withdrawMoney}>Withdraw</button>
        </>
      }  
    />
  )
};
export default Withdraw