import Tooltip from '@material-ui/core/Tooltip'
import { Button } from '@material-ui/core';
import '../App.css';
import {ReactLogo} from './Logo'
function NavBar({isLoggedIn}){
  return(
    <>
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark"> 
      <ReactLogo/>
        <a className="navbar-brand" href="#/">React State Bank</a>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            {isLoggedIn ? (  
              <>
                <Tooltip title="Deposit funds into your account's balacnce" enterDelay={500} leaveDelay={200}>
                  <Button>
                    <li className="nav-item">
                      <a className="nav-link" href="#/deposit/" > Deposit </a>
                    </li>
                  </Button>
                </Tooltip>
                <Tooltip title="Withdraw funds from your account's balance" enterDelay={500} leaveDelay={200}>
                  <Button>
                    <li className="nav-item">
                      <a className="nav-link" href="#/withdraw/"> Withdraw </a>
                    </li>
                  </Button>
                </Tooltip>
                <Tooltip title="Login-out from your account" enterDelay={500} leaveDelay={200}>
                  <Button>
                    <li className="nav-item">
                      <a className="nav-link" href="#/logout/"> Logout </a>
                    </li>  
                  </Button>
                </Tooltip>
                <Tooltip title="Click here to see the users data" enterDelay={500} leaveDelay={200}>
                  <Button>
                    <li className="nav-item">
                      <a className="nav-link" href="#/alldata/"> All Data </a>
                    </li> 
                  </Button>
                </Tooltip>
              </>
            ):(
          <>
            <Tooltip title="Create an account to be able to Login" enterDelay={500} leaveDelay={200}>
              <Button>
            <li className="nav-item">
              <a className="nav-link" href="#/CreateAccount/" >  Create Account</a>
            </li>
            </Button>
            </Tooltip>
          <Tooltip title="Login-in to access your account" enterDelay={500} leaveDelay={200}>
            <Button>
              <li className="nav-item">
                <a className="nav-link" href="#/login/" >  Login</a>
              </li>
            </Button>
          </Tooltip>
          <Tooltip title="Click here to see the users data" enterDelay={500} leaveDelay={200}>
            <Button>
          <li className="nav-item">
            <a className="nav-link" href="#/alldata/">
              All Data
            </a>
          </li> 
          </Button>
          </Tooltip>
    </>
  )}
        </ul>
      </div>
    </nav>
    </>
  );
}

export default NavBar;