import React from 'react'
import { AtmObject } from '../App'
import Card from './card'
import {ReactLogo} from './Logo'

function AllData(){
  const atmObject = React.useContext(AtmObject)
  const {accounts} = atmObject;  
  return (
    <Card
    bgcolor='info'
    txtcolor="white"
    header="ALL DATA"  
    body= {
      <table className="card-table table">
    <thead>
      <tr>
        <th scope="col">Email</th>
        <th scope="col">Name</th>
        <th scope="col">Password</th>
      </tr>
    </thead>
    <tbody>
      {Object.keys(accounts).map((key)=> {
        return  <tr key={accounts[key].email}>
        <td>{accounts[key].email}</td>
        <td>{accounts[key].name}</td>
        <td>{accounts[key].password}</td>
      </tr>
      })}
    </tbody>
    <ReactLogo/>
  </table>
    } 
  />    
); 
}
export default AllData;
