import Card from './card'
import React from 'react'
import { AtmObject } from '../App'
import SimpleSlider from './carousel'
import SimpleSliderLoggedIn from './loggedCarousel'


function Home(){
  const atmObject = React.useContext(AtmObject);
  const {accounts, user, isLoggedIn} = atmObject
  
  return (
    <Card
      bgcolor='secondary'
      txtcolor="white"
      header="React's State Bank"
      body={isLoggedIn ? (
          <div>
            <br/>
            <h1>Welcome {accounts[user].name}!</h1>
            <br/>
            <h2 id = "account-balance">Your Account Balance is: <span className='balance-amount'>${(accounts[user].accountBalance)}</span></h2>
            <br/>
            <SimpleSliderLoggedIn/>          
          </div>
      ):(      
        <div className="header-bar">
            <h2 style={{padding:'0 20 0 20'}}id="welcome">Welcome to React State Bank</h2> 
            <SimpleSlider/>
        </div>
        )
      }  
    />        
  );   
}

export default Home